import React from 'react';

const InputForm = ({ onChange, inputValues }) => {
  const handleChange = (section, key, value) => {
    onChange(section, { ...inputValues[section], [key]: value });
  };

  const operationalCostKeys = ['fuel', 'labor', 'insurance', 'maintenance', 'software', 'vehicleLoan', 'otherLoans'];

  const calculateWeeklyCost = (monthlyCost) => (monthlyCost / 4.33).toFixed(2);

  return (
    <div className="space-y-4">
      <div>
        <h2 className="text-xl font-bold">Initial Investment</h2>
        <input
          type="number"
          placeholder="Vehicle"
          onChange={(e) => handleChange('initialInvestment', 'vehicle', e.target.value)}
          className="border p-2 w-full"
        />
        <input
          type="number"
          placeholder="Treadmills"
          onChange={(e) => handleChange('initialInvestment', 'treadmills', e.target.value)}
          className="border p-2 w-full"
        />
        <input
          type="number"
          placeholder="Branding"
          onChange={(e) => handleChange('initialInvestment', 'branding', e.target.value)}
          className="border p-2 w-full"
        />
        <input
          type="number"
          placeholder="Website"
          onChange={(e) => handleChange('initialInvestment', 'website', e.target.value)}
          className="border p-2 w-full"
        />
        <input
          type="number"
          placeholder="Booking Software"
          onChange={(e) => handleChange('initialInvestment', 'bookingSoftware', e.target.value)}
          className="border p-2 w-full"
        />
        <input
          type="number"
          placeholder="Other"
          onChange={(e) => handleChange('initialInvestment', 'other', e.target.value)}
          className="border p-2 w-full"
        />
        <p className="mt-2 font-bold">
          Total Initial Investment: ${Object.values(inputValues.initialInvestment).reduce((acc, val) => acc + parseFloat(val || 0), 0)}
        </p>
      </div>
      <div>
        <h2 className="text-xl font-bold">Estimated Monthly Operational Cost</h2>
        {operationalCostKeys.map((key) => (
          <div key={key} className="flex items-center">
            <input
              type="number"
              placeholder={key.charAt(0).toUpperCase() + key.slice(1)}
              onChange={(e) => handleChange('operationalCosts', key, e.target.value)}
              className="border p-2 w-full"
            />
            <span className="ml-2">
              Weekly: ${calculateWeeklyCost(inputValues.operationalCosts[key] || 0)}
            </span>
          </div>
        ))}
        <p className="mt-2 font-bold">
          Total Monthly Operational Cost: ${Object.values(inputValues.operationalCosts).reduce((acc, val) => acc + parseFloat(val || 0), 0)}
        </p>
        <p className="font-bold">
          Weekly Operational Cost: $
          {(
            Object.values(inputValues.operationalCosts).reduce((acc, val) => acc + parseFloat(val || 0), 0) / 4.33
          ).toFixed(2)}
        </p>
      </div>
      <div>
        <h2 className="text-xl font-bold">Revenue Streams (Monthly)</h2>
        <div className="flex items-center">
          <input
            type="number"
            placeholder="Individual Session Price"
            onChange={(e) => handleChange('revenueStreams', 'individualSessionPrice', e.target.value)}
            className="border p-2 w-full"
          />
          <input
            type="number"
            placeholder="Estimated Customers"
            onChange={(e) => handleChange('revenueStreams', 'individualSessionCustomers', e.target.value)}
            className="border p-2 w-full ml-2"
          />
          <span className="ml-2">
            Weekly Revenue: $
            {(
              (parseFloat(inputValues.revenueStreams.individualSessionPrice || 0) *
                parseFloat(inputValues.revenueStreams.individualSessionCustomers || 0)) /
              4.33
            ).toFixed(2)}
          </span>
        </div>
        <label className="block mt-4">
          <input
            type="checkbox"
            onChange={(e) => handleChange('revenueStreams', 'subscriptionsEnabled', e.target.checked)}
            className="mr-2"
          />
          Enable Subscriptions
        </label>
        {inputValues.revenueStreams.subscriptionsEnabled && (
          <div className="space-y-2">
            <input
              type="number"
              placeholder="Subscription Price"
              onChange={(e) => handleChange('revenueStreams', 'subscriptionPrice', e.target.value)}
              className="border p-2 w-full"
            />
            <input
              type="number"
              placeholder="Number of Subscriptions"
              onChange={(e) => handleChange('revenueStreams', 'subscriptionCustomers', e.target.value)}
              className="border p-2 w-full"
            />
            <input
              type="number"
              placeholder="Avg Sessions per Subscription"
              onChange={(e) => handleChange('revenueStreams', 'subscriptionSessions', e.target.value)}
              className="border p-2 w-full"
            />
            <input
              type="number"
              placeholder="Avg Session Cost with Subscription"
              onChange={(e) => handleChange('revenueStreams', 'subscriptionSessionCost', e.target.value)}
              className="border p-2 w-full"
            />
            <p className="font-bold">
              Total Monthly Subscription Revenue: $
              {(
                parseFloat(inputValues.revenueStreams.subscriptionPrice || 0) *
                  parseFloat(inputValues.revenueStreams.subscriptionCustomers || 0) +
                parseFloat(inputValues.revenueStreams.subscriptionSessionCost || 0) *
                  parseFloat(inputValues.revenueStreams.subscriptionSessions || 0) *
                  parseFloat(inputValues.revenueStreams.subscriptionCustomers || 0)
              ).toFixed(2)}
            </p>
          </div>
        )}
        <label className="block mt-4">
          <input
            type="checkbox"
            onChange={(e) => handleChange('revenueStreams', 'contractsEnabled', e.target.checked)}
            className="mr-2"
          />
          Enable Contracts
        </label>
        {inputValues.revenueStreams.contractsEnabled && (
          <div className="space-y-2">
            <input
              type="number"
              placeholder="Contract Price"
              onChange={(e) => handleChange('revenueStreams', 'contractPrice', e.target.value)}
              className="border p-2 w-full"
            />
            <input
              type="number"
              placeholder="Number of Contracts"
              onChange={(e) => handleChange('revenueStreams', 'contractCustomers', e.target.value)}
              className="border p-2 w-full"
            />
            <p className="font-bold">
              Total Monthly Contract Revenue: $
              {(
                parseFloat(inputValues.revenueStreams.contractPrice || 0) *
                parseFloat(inputValues.revenueStreams.contractCustomers || 0)
              ).toFixed(2)}
            </p>
          </div>
        )}
        <p className="mt-4 font-bold">
          Total Monthly Revenue: $
          {(
            (parseFloat(inputValues.revenueStreams.individualSessionPrice || 0) *
              parseFloat(inputValues.revenueStreams.individualSessionCustomers || 0)) +
            (inputValues.revenueStreams.subscriptionsEnabled
              ? (parseFloat(inputValues.revenueStreams.subscriptionPrice || 0) *
                  parseFloat(inputValues.revenueStreams.subscriptionCustomers || 0)) +
                (parseFloat(inputValues.revenueStreams.subscriptionSessionCost || 0) *
                  parseFloat(inputValues.revenueStreams.subscriptionSessions || 0) *
                  parseFloat(inputValues.revenueStreams.subscriptionCustomers || 0))
              : 0) +
            (inputValues.revenueStreams.contractsEnabled
              ? parseFloat(inputValues.revenueStreams.contractPrice || 0) *
                parseFloat(inputValues.revenueStreams.contractCustomers || 0)
              : 0)
          ).toFixed(2)}
        </p>
      </div>
    </div>
  );
};

export default InputForm;
